import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './Home.css';

class Home extends Component {



  render() {
    console.log(JSON.stringify(this.props));
    const isLoggedIn = this.props.authenticated;
    console.log("Authenticated " + isLoggedIn);
    /* todo birta mismunandi upplsingar ef innskrur notandi ea ekki */
    const loggedInDiv = (
      <div className="home-text">
        <p>
           ert skrur notandi og getur v <Link to='/books/new'>skr bkur</Link>
          og <Link to='/books'>breytt eim sem til eru</Link>.
        </p>
        <p>
          Einnig getur  skoa <Link to='/users'>ara notendur</Link>.
        </p>
      </div>
    );

    const loggedOutDiv = (
      <div className="home-text">
      <p>
        Til a njta bkasafnsins til fullnustu mlum vi me a <Link to='/login'>skr sig inn</Link>.
        anga til getur  skoa <Link to='/books'>allar bkurnar</Link>.
      </p>
      </div>
    );
    return (
      <div className="home">
        <h1>Velkominn  bkasafni</h1>
        {isLoggedIn ? loggedInDiv : loggedOutDiv}
      </div>
    );
  }
}

/* todo setja upp tengingu vi redux til a vita stu notanda */
export default Home;



