import api from '../api';

export const LOGIN_REQUEST = 'LOGIN_REQUEST';
export const LOGOUT_REQUEST = 'LOGIN_REQUEST';

export function requestLogin(auth) {
  return {
    type: LOGIN_REQUEST,
    payload: auth,
  };
}

export function requestLogout(auth) {
  return {
    type: LOGIN_REQUEST,
    payload: {},
  };
}

